<template>
    <article class="city-weather">
      <app-container v-if="isDataLoaded" class="city-weather__container">
        <h1>Погода Сегодня</h1>
        <app-current-weather 
        :name="city"
        :currentData="currentCityData" 
        class="city-weather__crnt-weather"></app-current-weather>
        <h1>Погода на неделю</h1>
        <app-daily-weather
        :days="dailyWeather"
        class="city-weather__daily-weather"
        >

        </app-daily-weather>
      </app-container>
    <div v-else class="loading-screen">
      <h1>Идет загрузка...</h1>
    </div> 
    </article>
</template>
<script>
import AppCurrentWeather from '@/components/AppCurrentWeather'
import AppContainer from '@/components/AppContainer'
import AppDailyWeather from "@/components/AppDailyWeather"
export default {
  components: {
    AppContainer,
    AppCurrentWeather,
    AppDailyWeather,
  },
  data() {
    return {
      isDataLoaded: true,
      lon: '',
      lat: '',
      city: '',
      currentCityData: {
        month: '',
        date: '',
        time: '',
        temp: '',
        windSpeed: '',
        windDir: '',
        cloudness: '',
        pressure: '',
        humidity: '',
        feels_like: '',
        description: '',
        icon: null,
      },
      dailyWeather: [

      ]
      
    }
  },

  methods: {
    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },

    async getGeolocation() {
      let clientCityNameGET = await fetch('https://api.db-ip.com/v2/free/self');
      let cliendCityNameData = await clientCityNameGET.json();
      console.log(cliendCityNameData);
      let clientCityLocationGET = await fetch(`https://api.openweathermap.org/geo/1.0/direct?q=${ cliendCityNameData['city']}&limit=2&appid=84bea86b6e84affc971c5ed48003ecea`)
      let clientCityLocationData = await clientCityLocationGET.json()
      let {lon, lat} = clientCityLocationData[0]
      this.lon = lon,
      this.lat = lat
    },


    async getDailyWeather() {
      fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.lat}&lon=${this.lon}&units=metric&lang=ru&exclude=current,minutely,hourly,alerts&appid=84bea86b6e84affc971c5ed48003ecea`)
      .then(response => {
        return response.json()
      })
      .then(data => {
        let week = data.daily.slice(1)
        for (let day of week) {
            this.createWeatherDataObject(day)
        }
      })
    },

    async getCurrentWeather() {
      fetch(`https://api.openweathermap.org/data/2.5/weather?lat=${this.lat}&lon=${this.lon}&units=metric&lang=ru&appid=84bea86b6e84affc971c5ed48003ecea`)
      .then(response => {
        return response.json()
      })
      .then(data => {
        this.setCurrentCityData(data)
      })
    },


    createWeatherDataObject(data) {
        let date = new Date(data.dt * 1000)
        let weekDays = ['ВС', 'ПН', 'ВТ', 'СР', 'ЧТ', 'ПТ', 'СБ',]
        let obj = {
          id: Date.now(),
          weekDay: weekDays[date.getDay()],
          day: date.getDate(),
          temp: `${Math.trunc(data.temp.day)}°C`,
          clouds: `${data.clouds}%`,
          icon: `https://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`,
          descr: this.capitalizeFirstLetter(data.weather[0].description)
        }
        this.dailyWeather.push(obj)
      },

    setCurrentCityData(data) {
      let date = new Date(data.dt * 1000 + data.timezone * 1000) ;
      this.city = data.name.toUpperCase();
      let months = ['ЯНВ', 'ФЕВ', 'МАР', 'АПР', 'МАЙ', 'ИЮН', 'ИЮЛ', 'АВГ', 'СЕН', 'ОКТ', 'НОЯ', 'ДЕК',] 
      this.currentCityData.month = months[date.getMonth()]
      this.currentCityData.date = date.getDate()
      let minutes = date.getMinutes()
      minutes = minutes.toString().length == 1 ? '0' + minutes : minutes
      this.currentCityData.time = `${date.getUTCHours()}:${minutes}`
      this.currentCityData.temp = `${Math.trunc(data.main.temp)}°C`
      this.currentCityData.windSpeed = `${data.wind.speed} м/с`
      this.currentCityData.windDir = convertWindDeg(data.wind.deg)
      this.currentCityData.cloudness = `${data.clouds.all}%`
      this.currentCityData.pressure = `${data.main.pressure * 0.75} мм.рт.ст.`
      this.currentCityData.humidity = `${data.main.humidity}%`
      this.currentCityData.feels_like = `${Math.trunc(data.main.feels_like)}°C`;
      this.currentCityData.description = this.capitalizeFirstLetter(data.weather[0].description)
      this.currentCityData.icon = `https://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`

      function convertWindDeg(value) {
        let degrees = 
          {    
          23: 'C', 
          45: 'СВ',
          68: 'СВ',
          90: 'В',
          113: 'В',
          135: 'ЮВ',
          158: 'ЮВ',
          180: 'Ю',
          203: 'Ю',
          225: 'ЮЗ',
          248: 'ЮЗ',
          270: 'З',
          293: 'З',
          315: 'СЗ',
          338: 'СЗ',
          360: 'С',
        }
        let keys= Object.keys(degrees)
        let dir = keys.find(d => value < d)
        return degrees[dir]
      }
    }

  },

   async mounted(){
     this.isDataLoaded = false;
    await this.getGeolocation()
    await this.getCurrentWeather()
    await this.getDailyWeather()
     this.isDataLoaded = true;
  },
}
</script>
<style>
    .city-weather__container {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 1500px;
    }

    .city-weather__crnt-weather {
      margin: 25px 0;
    }

    .city-weather__daily-weather {
      margin-top: 25px;
    }

    .loading-screen {
      text-align: center;
    }
</style>